
@include keyframes(translateTopIn) {
  from {
    opacity: 0;
    transform: translateY(-100%) rotate(-10deg);
  }

  to {
    opacity: 1;
    transform: translateY(0) rotate(0);
  }
}

@include keyframes(translateTopOut) {
  from {
    opacity: 1;
   transform: translateY(0) rotate(0);
  }

  to {
    opacity: 0;
   transform: translateY(-100%) rotate(-10deg);
  }
}

@include keyframes(translateBottomIn) {
  from {
    opacity: 0;
    transform: translateY(100%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@include keyframes(animateFadeIn) {
  from {
    opacity: 0;
    transform: translateY(10%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}


@include keyframes(navbarIn) {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@include keyframes(navbarOut) {
  from {
    opacity: 1;
    transform: translateY(0);
  }

  to {
    opacity: 0;
    transform: translateY(-100%);
  }
}
